.service-1
	.wrapper-main
		@screen -lg
			@apply grid-cols-1
		@screen xl
			&:nth-child(even)
				.col-left
					order: 2
				.col-right
					order: 1
		.col-right
			.box
				background: rgba(242, 159, 5, 0.08)
				@apply xl:p-20 p-5 rounded-5 xl:rem:max-h-[440px] h-full
				.format-content
					@apply overflow-auto pr-2 xl:rem:max-h-[180px] h-full
					p
						line-height: calc(28/20)
						@apply mb-0
.service-3
	background: rgba(242, 159, 5, 0.08)