.section-list-text-image
  @apply flex flex-col gap-15 -xl:gap-base
  .item-text-image
    .box-item
      @apply -lg:container flex items-center -md:flex-col rem:gap-x-[100px] -xl:gap-x-10 -lg:gap-x-5 -md:gap-y-5
      .box-image
        @apply xl:rem:max-w-[960px] w-full flex-1 zoom-img
      .box-content
        @apply flex flex-col items-start gap-base lg:rem:w-[600px] md:w-1/2
        .row
          @apply gap-y-5 
          .sub-title
            @apply heading-3 text-primary-1
          .title
            @apply heading-1 text-primary-1
          .format-content
            @apply body-1 text-utility-gray-950
    &:nth-child(even)
      .box-image
        @apply order-2 -md:order-[unset]
      .box-content
        @apply xl:rem:pl-[260px] pl-5 
        .col-xl-6
          @apply -xl:pl-3