.about-3
	.swiper-column-auto
		.swiper-slide
			--spv: 3
			--mr: calc(8/1920*100rem)
			@apply flex flex-col h-auto
			@screen sm
				--spv: 3
				--mr: calc(16/1920*100rem)
			@screen md
				--spv: 3
				--mr: calc(16/1920*100rem)
			@screen lg
				--spv: 4
				--mr: calc(24/1920*100rem)
			@screen xl
				--spv: 5
				--mr: calc(20/1920*100rem)