.section-banner-home
  @apply xl:-mt-5
  .box-slide
    @apply relative zoom-img 
    .box-image
      &::after
        content:''
        @apply absolute-full bg-black/20 
    .box-content
      @apply absolute-center text-center text-utility-white w-[70%] -md:w-[95%]
      .sub-title
        @apply heading-1 font-normal
        @media -md
          .sub-title
            @apply rem:text-[30px] font-primary #{!important}
      .title
        @apply relative xl:rem:text-[96px] heading-banner font-medium font-banner
        &::before
          @apply content-[''] w-full h-[2px] bg-linear-2 rounded-full 
          @apply left-0 top-0 translate-y-full absolute
  .swiper-pagination-bullet
    @apply bg-utility-white opacity-50
    &.swiper-pagination-bullet-active
      @apply bg-utility-white opacity-100

main
  @apply overflow-x-hidden -xl:w-screen
  // @apply overflow-auto animate-spin duration-1000