.global-breadcrumb
	@apply bg-utility-gray-50
	p
		@apply h-[calc(48/1920*100rem)] flex items-center overflow-auto
		a,span
			@apply text-sm text-utility-gray-500 whitespace-nowrap
		span.separator
			@apply text-0 inline-flex
			&::before
				content: "\f054"
				@apply font-awesome font-normal text-sm mx-2

