.wrap-form-search-product
	width: 100%
	max-width: 600px
	margin: 0 auto

.productsearchbox
	display: flex
	align-items: center
	gap: 0
	border: 1px solid #E5E7EB
	border-radius: 8px
	overflow: hidden
	background: white
	transition: all 0.3s ease

	&:focus-within
		border-color: var(--primary-1)
		box-shadow: 0 0 0 3px rgba(var(--primary-1-rgb, 0, 87, 63), 0.1)

	input[type="text"]
		flex: 1
		padding: 12px 16px
		border: none
		outline: none
		font-size: 16px
		color: #1F2937
		background: transparent

		&::placeholder
			color: #9CA3AF
			font-size: 14px

	button
		padding: 12px 20px
		background: var(--primary-1)
		color: white
		border: none
		cursor: pointer
		transition: all 0.3s ease
		display: flex
		align-items: center
		justify-content: center
		min-width: 50px

		&:hover
			background: var(--primary-2)

		i
			font-size: 18px
