@layer components
    form.submitting
        button
            @apply pointer-events-none relative
            &::before
                content: ''
                animation-duration: 0.5s
                @apply w-5 h-5 border-3 border-primary-1/40 border-t-primary-1 rounded-full inline-block
                @apply absolute animate-spin left-full ml-3
    // grid res
    .grid-col-5-res
        @apply grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5
    .grid-col-4-res-2
        @apply grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4
    .grid-col-4-res-1
        @apply grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4
    #modal
        @apply fixed z-[999] inset-0 translate-x-full bg-black/40 opacity-0 invisible transition-300 delay-[0.3s] 
    // hover link
    .hover-link
        @apply relative pb-0.5
        &::after
            @apply content-[''] absolute bottom-0 left-0 inline-block w-0 border-b border-transparent opacity-0 transition-300
        &:hover::after
            @apply w-full border-white/50 opacity-100
    .section-product-category
        .category-name
            @apply h-10 px-5 bg-[#9E3632] heading-3 text-white uppercase
            @apply flex items-center mb-6
    .our-product-item
        .content
            @apply transition-all ease-in-out
        &:hover
            .content
                @apply bg-primary-1 
                .title
                    @apply text-white
    #form-info-product
        @apply rem:max-w-[1400px] w-full  relative xl:p-10 p-5 bg-utility-gray-50
        form
            @apply w-full
        .carousel__button
            position: absolute !important
            top: 0.5rem !important
            right: 1rem !important
            @apply w-10 h-10 bg-Primary-1 hidden
            svg
                @apply rem:w-[20px] rem:h-[20px]
        .wrapper-main
            .col-right
                &::-webkit-scrollbar
                    @apply w-[calc(4/1920*100rem)]
                &::-webkit-scrollbar-track
                    background-color: #D9D9D9
                &::-webkit-scrollbar-thumb
                    @apply bg-primary-1
    .card-product
        @apply flex flex-col flex-1
        .img
            @apply rounded-4 bg-Primary-2 bg-opacity-[0.08]
        .content-card-product
            @apply py-4 relative flex flex-col flex-1
            &::before
                content: ""
                @apply absolute left-0 bottom-0 w-full h-[2px] bg-linear-2
            &::after
                content: ""
                @apply absolute left-0 bottom-0 w-full h-[2px] bg-utility-gray-300 transition-all duration-300
                @apply heading-4 font-bold duration-300
        &:hover
            .content-card-product::after
                @apply opacity-0
        .title-card-product
            @apply heading-4 font-bold duration-300
    .item-news
        .box-content
            @apply flex-between flex-col gap-3 mt-5

            .title
                @apply heading-4 text-utility-gray-950 line-clamp-2 transition-300

            .time-news
                @apply body-4 text-utility-gray-500
        &:hover
            .box-content
                .title
                    @apply text-primary-1
    .slide-service
        .swiper-column-auto
            .swiper-slide
                --spv: 2
                @screen sm
                    --spv: 1
                @screen md
                    --spv: 3
                @screen lg
                    --spv: 4
                    --mr: calc(40/1920*100rem)
                @apply flex flex-col h-auto
    .pagination
        @apply flex items-center justify-center
        ul
            @apply flex items-center justify-center gap-6
            li
                a, span
                    @apply inline-flex items-center justify-center
                    @apply sq-12 rounded-1
                    @apply bg-transparent border-2 border-utility-gray-500
                    @apply  heading-4 font-bold text-utility-gray-500
                &.active, &:hover
                    a, span
                        @apply bg-Primary-1 text-white border-transparent
    .nav-primary
        @apply flex items-center justify-center gap-2
        @apply overflow-auto whitespace-nowrap
        @screen -md
            @apply justify-normal pb-3
        li
            a
                @apply inline-flex items-center justify-center 
                @apply px-6 h-11 rounded-full border border-Primary-2
                @apply bg-white text-Primary-2 font-bold
            &.active, &:hover
                a
                    @apply bg-Primary-2 text-white