.box-image-full
  @apply relative rem:min-h-[683px] -lg:rem:min-h-[500px]
  @apply section-py
  &::after
    content: ''
    @apply absolute inset-0 bg-gradient-bg z-1
  .box-content
    @apply container flex-center text-white
    @screen -md
      @apply pl-0
    .img-gradient-bg
      @apply absolute rem:w-[77px] -left-10 rem:top-[120px] hidden
    .title-banner
      @apply md:rem:text-[88px] heading-banner font-black
    .title
      @apply heading-2 text-utility-white 
    .format-content
      p
        @apply mb-10 -lg:mb-5