.news-1
	@apply pb-10 
	@screen -md
		@apply py-10
	.news-1-main
		@screen -lg
			@apply flex-col
			.news-left
				@apply max-w-full
			.news-right
				.news-content
					@apply p-10 gap-4
					
		.news-right				
			.news-date
				span
					&:first-child
						@apply text-xl font-bold
					&:last-child
						@apply rem:text-[14px]
			.news-box
				.title-24
					@apply rem:text-[24px]
.news-2
	@screen -md
		@apply pb-10
	.news-2-list
		@screen -md
			@apply px-[16px]
	.news-item
		.content
			.mid
				.title
					@apply text-xl
			.top
				.date
					@apply hidden
			.bottom
				.desc
					@apply line-clamp-3 text-lg