.btn
	@apply cursor-pointer select-none transition-all duration-300
	&.loading
		@apply bg-transparent #{!important}
		span,.icon
			@apply opacity-0
		&::before
			content: ''
			animation-duration: 0.5s
			@apply w-5 h-5 border-3 border-white/40 border-t-white rounded-full inline-block
			@apply absolute animate-spin
	&.btn-more
		@apply h-12 bg-primary-1 rounded-1 px-6 text-white
		@apply inline-flex items-center justify-center gap-2
		span
			@apply body-2 font-bold
		.icon
			@apply sq-6 inline-flex items-center justify-center 
			@apply transition-transform ease-in-out
			i
				@apply body-1
		&:hover
			@apply bg-primary-2
			.icon
				@apply translate-x-1
	&.btn-sw-1
		@apply sq-10 py-2 px-5 rounded-full flex-center bg-primary-1
		&::before
			@apply font-awesome text-white font-light text-xl
		&.btn-prev
			&::before
				content: '\f060'
		&.btn-next
			&::before
				content: '\f061'
				@apply font-awesome
		&:hover
			@apply bg-primary-4
			&::before
				@apply text-white
